import {
  ChangeDetectionStrategy,
  Component,
  Input,
  OnInit,
} from '@angular/core';
import { Observable } from 'rxjs';

import { IStatement2Transaction, TRANSACTION_TYPE } from '../../models';
import { Statement2Service } from '../../services';

@Component({
  selector: 'j-statement2-transaction',
  templateUrl: './statement2-transaction.component.html',
  styleUrls: ['./statement2-transaction.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class Statement2TransactionComponent implements OnInit {
  @Input() transaction: IStatement2Transaction;

  isCredit: boolean;
  date$: Observable<string>;

  constructor(private statement2Service: Statement2Service) {}

  ngOnInit() {
    this.mapTransactionType();
    this.mapDate();
  }

  private mapDate() {
    this.date$ = this.statement2Service.mapTransactionDate(this.transaction.documentDate);
  }

  private mapTransactionType() {
    this.isCredit = this.transaction.type === TRANSACTION_TYPE.CREDIT;
  }
}
