import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  TemplateRef,
} from '@angular/core';

import dayjs from 'dayjs';

import {
  IStatement2Group,
  IStatement2Transaction,
  TRANSACTION_GROUP_TYPE,
} from '../../models';

@Component({
  selector: 'j-statement2-group',
  templateUrl: './statement2-group.component.html',
  styleUrls: ['./statement2-group.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class Statement2GroupComponent implements OnInit {
  @Input() group: IStatement2Group;
  @Input() groupType: TRANSACTION_GROUP_TYPE;

  @Input() groupHeaderTemplate: TemplateRef<any>;
  @Input() transactionTemplate: TemplateRef<any>;

  @Output() clicked = new EventEmitter<IStatement2Transaction>();

  title: string;

  constructor() {}

  ngOnInit() {
    this.mapTitle();
  }

  onClick(transaction: IStatement2Transaction) {
    this.clicked.emit(transaction);
  }

  private mapTitle() {
    switch (this.groupType) {
      case TRANSACTION_GROUP_TYPE.MONTH:
        let monthName = dayjs(this.group.groupDate).format('MMMM');
        this.title = `${monthName[0].toUpperCase()}${monthName.slice(1)}`;
        break;

      default:
      case TRANSACTION_GROUP_TYPE.DAY:
        this.title = dayjs(this.group.groupDate).format('DD.MM.YYYY');
        break;
    }
  }
}
