import { IMoney } from '@shared/currency/currency.model';
import { IExchangeRate, IKbe, IKBK, IKNP } from '@shared/models';

export enum TRANSACTION_TYPE {
  CREDIT = 'CREDIT',
  DEBIT = 'DEBIT',
}

export enum TRANSACTION_CATEGORY {
  COMMISSION = 'COMMISSION',
  REFUND = 'REFUND',
  TRANSFER = 'TRANSFER',
}

export enum TRANSACTION_GROUP_TYPE {
  DAY = 'DAY',
  MONTH = 'MONTH',
  NONE = 'NONE',
}

export interface IStatement2TransactionRefundDetails {
  account: string;
  amount: IMoney;
  bin: string;
  birthDate: Date;
  date: Date;
  description: string;
  iban: string;
  knp: string;
  
  firstName: string;
  lastName: string;
  middleName: string;
  
  period: string;
  parentRef: string;
  refundRef: string;
}

export interface IStatement2Transaction {
  amount: number;
  amountLCY: number;
  amountVat: number;
  
  category: TRANSACTION_CATEGORY;
  commissionFlag: string;

  correspondentAccount: string;
  correspondentBankBic: string;
  correspondentBankName: string;
  correspondentBin: string;
  correspondentName: string;
  
  credit: IMoney;
  debit: IMoney;

  description: string;
  
  documentDate: Date;
  documentNumber: string;
  documentType: string;

  externalId: string;
  iban: string;
  id: string;
  
  kbe: IKbe;
  kbk: IKBK;
  knp: IKNP;
  
  parentReference: string;
  paymentColvirId: string;

  reference: string;
  referenceKisc: string;
  referenceRelatedDocument: string;
  
  refundDetails: IStatement2TransactionRefundDetails;
  
  signBuch: string;
  signHead: string;
  
  type: TRANSACTION_TYPE;
  valueDate: Date;
}

export interface IStatement2Group {
  groupDate: Date;

  debitSum: IMoney;
  creditSum: IMoney;

  exchangeRate1: IExchangeRate;
  exchangeRate2: IExchangeRate;

  transactionsCount: number;

  transactions: IStatement2Transaction[];
}

export interface IStatement2 {
  iban: string;
  
  statementDate: Date;
  dateFrom: Date;
  dateTo: Date;
  
  balanceIn: IMoney;
  balanceOut: IMoney;
  balanceInLCY: IMoney;
  balanceOutLCY: IMoney;
  
  debitTotalSum: IMoney;
  creditTotalSum: IMoney;

  groupType: TRANSACTION_GROUP_TYPE;

  totalTransactionsCount: number;

  transactionGroups: IStatement2Group[];
}
