import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Observable, Subject, Subscription } from 'rxjs';
import {
  concatMap,
  distinctUntilChanged,
  map,
  switchMap,
  take,
  takeWhile,
} from 'rxjs/operators';

import { convertDateToApiFormat } from '@utils/dates';

import {
  FILTER_COMISSION,
  FILTER_MODE,
  FILTER_SORT,
  IFilterScheme,
  OPERATION_CODE,
} from '@filter/filter.model';
import { FilterService } from '@filter/filter.service';

import { CURRENCY } from '@core/models';

import { IStatement2, IStatement2Options } from '@statement2/models';

import { MaskedService } from '@shared/masked/masked.service';
import { FilesService } from '@shared/services';

import { PAGE_COLS_CLASSLIST } from '../../config';
import { DepositsService } from '../../services';
import { AnalyticsManagerAbstract } from '@analytics/services/manager';

const MAX_PAGE_SIZE = 99999;

@Component({
  selector: 'j-deposit-statement',
  templateUrl: './deposit-statement.component.html',
  styleUrls: ['./deposit-statement.component.scss'],
})
export class DepositStatementComponent implements OnDestroy {
  filterMode = FILTER_MODE.DEPOSITS;
  colsClasslist = PAGE_COLS_CLASSLIST;

  iban: string;
  filter: IFilterScheme = this.filterService.getDefaults(FILTER_MODE.DEPOSITS);
  currency$: Observable<CURRENCY>;

  statement: IStatement2;
  statementOptions: IStatement2Options = {
    hasDetails: true,
  };

  hasError = false;
  emptyLabel: string = 'ACCOUNTS.EMPTY_STATEMENT';

  subscription = new Subscription();

  page$ = new Subject<number>();
  filter$ = new Subject<IFilterScheme>();

  page = 1;
  pageSize = 10;

  constructor(
    private analytics: AnalyticsManagerAbstract,
    private route: ActivatedRoute,
    private filterService: FilterService,
    private depositsService: DepositsService,
    private maskedService: MaskedService,
    private filesService: FilesService
  ) {
    this.route.parent.params.pipe(take(1)).subscribe((params) => {
      this.iban = params.iban;

      this.initSubscriptions();
      this.initStatementSubscriptions();
    });
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  onDownload(fileType: string) {
    this.analytics.logEvent('DepositStatement_Download_Click', {
      version: 1,
      type: fileType,
    });

    const filter = {
      ...this.mapFilter(this.filter),
      pageSize: this.statement?.totalTransactionsCount || MAX_PAGE_SIZE,
    };

    this.depositsService
      .downloadStatement(this.iban, filter, fileType)
      .pipe(take(1))
      .subscribe({
        next: ({ file, filename }) => {
          this.analytics.logEvent('DepositStatement_Download_Result', {
            version: 1,
            status: 200,
          });
          this.filesService.downloadFile(file, filename);
        },
        error: (error) => {
          this.analytics.logEvent('DepositStatement_Download_Result', {
            version: 1,
            status: 502,
            error,
          });
        },
      });
  }

  onRetry() {
    this.onFilter();
  }

  onFilter(filter: IFilterScheme = this.filter) {
    this.statement = null;
    this.hasError = false;
    this.page = 1;
    this.filter = { ...filter };
    this.filter$.next(filter);
  }

  onScroll() {
    this.page$.next(++this.page);
  }

  private initSubscriptions() {
    this.currency$ = this.depositsService.getDepositByIban(this.iban).pipe(
      map((deposit) => {
        return deposit.balance.currency;
      })
    );
  }

  private initStatementSubscriptions() {
    this.subscription.add(
      this.filter$
        .pipe(
          distinctUntilChanged(),
          map((filter: IFilterScheme) => {
            return this.mapFilter(filter);
          }),
          switchMap((filter) => {
            return this.depositsService.getStatement(this.iban, filter);
          })
        )
        .subscribe(
          (statement) => {
            this.statement = statement;
          },
          () => {
            this.hasError = true;
          }
        )
    );

    this.subscription.add(
      this.page$
        .pipe(
          distinctUntilChanged(),
          map((page) => {
            return { ...this.mapFilter(this.filter), page };
          }),
          concatMap((filter) =>
            this.depositsService.getStatement(this.iban, filter)
          ),
          takeWhile((statement) => statement.transactionGroups?.length > 0)
        )
        .subscribe((statement) => {
          if (
            this.statement.transactionGroups[
              this.statement.transactionGroups.length - 1
            ].groupDate === statement.transactionGroups[0].groupDate
          ) {
            this.statement = {
              ...this.statement,
              transactionGroups: [
                ...this.statement.transactionGroups.slice(0, -1),
                ...this.statement.transactionGroups.slice(-1).map((group) => {
                  return {
                    ...group,
                    transactions: [
                      ...group.transactions,
                      ...statement.transactionGroups[0].transactions,
                    ],
                  };
                }),
              ],
            };
          } else {
            this.statement = {
              ...this.statement,
              transactionGroups: [
                ...this.statement.transactionGroups,
                ...statement.transactionGroups,
              ],
            };
          }
        })
    );
  }

  private mapFilter(filter: IFilterScheme) {
    return {
      dateFrom: filter.periodFrom
        ? convertDateToApiFormat(filter.periodFrom)
        : null,
      dateTo: filter.periodTo ? convertDateToApiFormat(filter.periodTo) : null,
      documentNumber: filter.documentNumber,
      correspondentAccount: filter.recipientIban,
      correspondentBin: filter.recipientBin,
      type:
        filter.operationType != OPERATION_CODE.ALL
          ? filter.operationType
          : null,
      sort: filter.sortBy !== FILTER_SORT.DEFAULT ? filter.sortBy : null,
      amountFrom: filter.amountFrom
        ? this.maskedService.getNumberedAmount(filter.amountFrom)
        : null,
      amountTo: filter.amountTo
        ? this.maskedService.getNumberedAmount(filter.amountTo)
        : null,
      withCommission: filter.comission
        ? filter.comission === FILTER_COMISSION.WITH_COMISSION
          ? true
          : false
        : null,
      page: this.page,
      pageSize: this.pageSize,
    };
  }
}
