import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Subject, Subscription } from 'rxjs';
import {
  concatMap,
  distinctUntilChanged,
  map,
  switchMap,
  take,
  takeWhile,
} from 'rxjs/operators';

import { TranslateService } from '@ngx-translate/core';
import { NotifierService } from 'angular-notifier';
import dayjs from 'dayjs';

import { Deposit } from '@accounts/account.model';
import { AccountsService } from '@accounts/services';

import { IStatementDates } from '@filter/filter.model';

import { FilesService } from '@shared/services';

import { API_DATE_FORMAT, UI_DATE_FORMAT } from '@utils/dates';

import { PAGE_COLS_CLASSLIST } from '../../config';
import { IDepositInterests } from '../../models';
import { DepositsService } from '../../services';
import { AnalyticsManagerAbstract } from '@analytics/services/manager';

@Component({
  selector: 'j-deposit-interests',
  templateUrl: './deposit-interests.component.html',
  styleUrls: ['./deposit-interests.component.scss'],
})
export class DepositInterestsComponent implements OnDestroy {
  colsClasslist = PAGE_COLS_CLASSLIST;
  subscription = new Subscription();

  loadingCount = Array(3);

  deposit: Deposit;
  depositName: string;
  filterDates: IStatementDates;
  iban: string;
  hasError = false;

  page = 1;
  pageSize = 5;

  page$ = new Subject<number>();
  dates$ = new Subject<IStatementDates>();

  statement: IDepositInterests;

  constructor(
    private route: ActivatedRoute,
    private translateService: TranslateService,
    private accountsService: AccountsService,
    private analytics: AnalyticsManagerAbstract,
    private depositsService: DepositsService,
    private notifierService: NotifierService,
    private filesService: FilesService
  ) {
    this.route.parent.params.pipe(take(1)).subscribe((params) => {
      this.iban = params.iban;
      this.initSubscriptions();
    });
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  onKpnDownload() {
    this.notifierService.notify(
      'info',
      this.translateService.instant('DEPOSITS.INTERESTS.DOWNLOADING')
    );

    const filename = this.translateService.instant('DEPOSITS.KPN.FILENAME', {
      name: this.depositName,
      period: this.retrievePeriod(),
    });

    this.depositsService
      .downloadCertificate({
        iban: this.iban,
        dateFrom: dayjs(this.filterDates.from).format(API_DATE_FORMAT),
        dateTo: dayjs(this.filterDates.to).format(API_DATE_FORMAT),
      })
      .subscribe({
        next: (blob) => {
          this.analytics.logEvent('DepositMain_KPNCert_Result', {
            version: 1,
            status: 200,
          });
          this.filesService.downloadFile(blob, `${filename}.pdf`);
        },
        error: (error) => {
          this.analytics.logEvent('DepositMain_KPNCert_Result', {
            version: 1,
            status: 502,
            error,
          });
        },
      });
  }

  onDownload() {
    this.notifierService.notify(
      'info',
      this.translateService.instant('DEPOSITS.INTERESTS.DOWNLOADING')
    );

    const filename = this.translateService.instant(
      'DEPOSITS.INTERESTS.FILENAME',
      {
        iban: this.iban,
        period: this.retrievePeriod(),
      }
    );

    const filter = {
      page: this.page,
      pageSize: this.pageSize,
      dateFrom: dayjs(this.filterDates.from).format(API_DATE_FORMAT),
      dateTo: dayjs(this.filterDates.to).format(API_DATE_FORMAT),
    };

    this.depositsService
      .downloadDepositInterests(this.iban, filter)
      .pipe(take(1))
      .subscribe(
        {
          next: (blob) => {
            this.analytics.logEvent('MainReward_Download_Result', {
              version: 1,
              status: 200,
            });
            this.filesService.downloadFile(blob, `${filename}.pdf`);
          },
          error: (error)=>{
            this.analytics.logEvent('MainReward_Download_Result', {
              version: 1,
              status: 502,
              error
            });
          }
        }
      );
  }

  onRetry() {
    this.onFilter();
  }

  onFilter(dates: IStatementDates = this.filterDates) {
    this.page = 1;
    this.statement = null;
    this.hasError = false;
    this.dates$.next(dates);
  }

  onScroll() {
    this.page$.next(++this.page);
  }

  private initSubscriptions() {
    this.subscription.add(
      this.depositsService.getDepositByIban(this.iban).subscribe((deposit) => {
        this.deposit = deposit;
        this.filterDates = {
          from: dayjs(deposit.openDate).toDate(),
          to: dayjs().toDate(),
        };

        this.mapDepositName();
        this.initStatementSubscriptions();
      })
    );

    this.subscription.add(
      this.translateService.onLangChange.subscribe(() => {
        this.mapDepositName();
      })
    );
  }

  private initStatementSubscriptions() {
    this.subscription.add(
      this.dates$
        .pipe(
          distinctUntilChanged(),
          map((dates: IStatementDates) => {
            return {
              page: this.page,
              pageSize: this.pageSize,
              dateFrom: dayjs(dates.from).format(API_DATE_FORMAT),
              dateTo: dayjs(dates.to).format(API_DATE_FORMAT),
            };
          }),
          switchMap((filter) => {
            return this.depositsService.getDepositInterests(this.iban, filter);
          })
        )
        .subscribe({
          next: (statement) => {
            this.statement = statement;
          },
          error: () => {
            this.handleStatementError();
          },
          complete: () => {
            if (!this.statement) {
              this.handleStatementError();
            }
          },
        })
    );

    this.subscription.add(
      this.page$
        .pipe(
          distinctUntilChanged(),
          map((page) => {
            return {
              page,
              pageSize: this.pageSize,
              dateFrom: dayjs(this.filterDates?.from).format(API_DATE_FORMAT),
              dateTo: dayjs(this.filterDates?.to).format(API_DATE_FORMAT),
            };
          }),
          concatMap((filter) =>
            this.depositsService.getDepositInterests(this.iban, filter)
          ),
          takeWhile((statement) => statement.transactionGroups.length > 0)
        )
        .subscribe({
          next: (statement) => {
            this.handleStatementSuccess(statement);
          },
          error: () => {
            this.handleStatementError();
          },
          complete: () => {
            if (!this.statement) {
              this.handleStatementError();
            }
          },
        })
    );
  }

  private handleStatementError() {
    this.hasError = true;
  }

  private handleStatementSuccess(statement: IDepositInterests) {
    if (!this.statement && !statement) {
      this.handleStatementError();
    }

    if (
      this.statement.transactionGroups[
        this.statement.transactionGroups.length - 1
      ].groupDate === statement.transactionGroups[0].groupDate
    ) {
      this.statement = {
        ...this.statement,
        transactionGroups: [
          ...this.statement.transactionGroups.slice(0, -1),
          ...this.statement.transactionGroups.slice(-1).map((group) => {
            return {
              ...group,
              transactions: [
                ...group.transactions,
                ...statement.transactionGroups[0].transactions,
              ],
            };
          }),
        ],
      };
    } else {
      this.statement = {
        ...this.statement,
        transactionGroups: [
          ...this.statement.transactionGroups,
          ...statement.transactionGroups,
        ],
      };
    }
  }

  private retrievePeriod() {
    return `${dayjs(this.filterDates.from).format(UI_DATE_FORMAT)} - ${dayjs(
      this.filterDates.to
    ).format(UI_DATE_FORMAT)}`;
  }

  private mapDepositName() {
    this.depositName = this.accountsService.getDepositName(this.deposit);
  }
}
