import { Injectable } from '@angular/core';

import { of } from 'rxjs';

import { TranslateService } from '@ngx-translate/core';
import dayjs from 'dayjs';

import { IStatement2Transaction, IStatement2Options } from '../models';
import { defaultOptions } from '../config';

@Injectable({
  providedIn: 'root',
})
export class Statement2Service {
  constructor(private translateService: TranslateService) {}

  mapTransactionDate(transactionDate: Date) {
    const date = dayjs(transactionDate);

    if (date.isSame(dayjs(), 'day')) {
      return this.translateService.get(
        'DEPOSITS.INTERESTS.TRANSACTION.DATE.TODAY',
        {
          datetime: date.format('HH:mm'),
        }
      );
    } else if (
      dayjs(transactionDate).isSame(dayjs().subtract(1, 'day'), 'day')
    ) {
      return this.translateService.get(
        'DEPOSITS.INTERESTS.TRANSACTION.DATE.YESTERDAY',
        {
          datetime: date.format('HH:mm'),
        }
      );
    } else {
      return of(date.format('DD MMMM HH:mm'));
    }
  }

  mapStatementOptions(options: IStatement2Options) {
    return {
      ...defaultOptions,
      ...options,
    };
  }
}
