import { DOCUMENT } from '@angular/common';
import {
  Component,
  EventEmitter,
  Inject,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';

import { asideAnimations } from '@core/animations';

import {
  IStatement2,
  IStatement2Transaction,
  IStatement2Options,
} from './models';

import { Statement2Service } from './services';

@Component({
  selector: 'j-statement2',
  templateUrl: './statement2.component.html',
  styleUrls: ['./statement2.component.scss'],
  animations: asideAnimations,
})
export class Statement2Component implements OnInit, OnDestroy {
  @Input() statement: IStatement2;
  @Input() options: IStatement2Options;

  @Output() scrolled = new EventEmitter();

  activeTransaction: IStatement2Transaction;

  private _options: IStatement2Options;

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private statementService: Statement2Service
  ) {}

  ngOnInit() {
    this._options = this.statementService.mapStatementOptions(this.options);
  }

  ngOnDestroy(): void {
    this.document.body.classList.remove('overflow-hidden');
  }

  onScroll() {
    this.scrolled.emit();
  }

  onClick(transaction: IStatement2Transaction) {
    if (!this._options.hasDetails) {
      return;
    }

    this.document.body.classList.add('overflow-hidden');
    this.activeTransaction = transaction;
  }

  onClose() {
    this.activeTransaction = null;
    this.document.body.classList.remove('overflow-hidden');
  }
}
