import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

import { Deposit } from '@accounts/account.model';

import { FeatureFlagsService } from '@feature-flags/feature-flags.service';
import { FEATURE_FLAG } from '@feature-flags/feature-flags.model';

@Component({
  selector: 'j-deposit-header',
  templateUrl: './deposit-header.component.html',
  styleUrls: ['./deposit-header.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DepositHeaderComponent {
  @Input() iban: string;
  @Input() deposit: Deposit;

  isDepositOperationsDisabled$ = this.featureFlagsService.isDisabledStream(
    FEATURE_FLAG.DEPOSIT_OPERATIONS
  );

  constructor(private featureFlagsService: FeatureFlagsService) {}
}
