import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { IsFeatureEnabledGuard } from '@shared/guards/is-feature-enabled.guard';

import { FEATURE_FLAG } from '@feature-flags/feature-flags.model';

import {
  DepositComponent,
  DepositRequisitesComponent,
  DepositInterestsComponent,
  DepositStatementComponent,
  DepositTermsComponent,
} from './components';

const routes: Routes = [
  {
    path: ':iban',
    component: DepositComponent,
    children: [
      {
        path: '',
        redirectTo: 'statement',
      },
      {
        path: 'statement',
        component: DepositStatementComponent,
      },
      {
        path: 'interests',
        canActivate: [IsFeatureEnabledGuard],
        data: { featureFlag: FEATURE_FLAG.DEPOSIT_REWARDS },
        component: DepositInterestsComponent,
      },
      {
        path: 'terms',
        component: DepositTermsComponent,
      },
      {
        path: 'requisites',
        component: DepositRequisitesComponent,
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class DepositsRoutingModule {}
