import {
  animate,
  animateChild,
  query,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';

const wrapperInitialState = {
  opacity: 0,
  transform: 'scale(0.8)',
  transformOrigin: '50% 50%',
};

const wrapperFinalState = {
  opacity: 1,
  transform: 'scale(1)',
  transformOrigin: '50% 50%',
};

const duration = '200ms ease-in-out';

const childInitialState = {
  right: '-100%',
};

const childFinalState = {
  right: 0,
};

export const asideAnimations = [
  trigger('wrapperAnimations', [
    state('in', style(wrapperFinalState)),
    transition(':enter', [
      style(wrapperInitialState),
      animate(duration),
      query('@childAnimations', [animateChild()]),
    ]),
    transition(':leave', [
      query('@childAnimations', [animateChild()]),
      animate(duration, style(wrapperInitialState)),
    ]),
  ]),
  trigger('childAnimations', [
    state('in', style(childFinalState)),
    transition(':enter', [style(childInitialState), animate(duration)]),
    transition(':leave', [animate(duration, style(childInitialState))]),
  ]),
];
