import {
  ChangeDetectionStrategy,
  Component,
  Input,
  OnInit,
} from '@angular/core';

import { Observable, of } from 'rxjs';

import { TranslateService } from '@ngx-translate/core';
import { MaskPipe } from 'ngx-mask';

import { Masks } from '@shared/masked/masked';
import { MoneyPipe } from '@shared/pipes';

import { Statement2Service } from '@statement2/services';

import { IDepositInterestTransaction } from '../../models';
import { DepositsService } from '@pages/deposits/services';
import { NotifierService } from 'angular-notifier';
import dayjs from 'dayjs';
import { UI_DATE_FORMAT } from '@utils/dates';
import { take } from 'rxjs/operators';
import { FilesService } from '@shared/services';
import { AnalyticsManagerAbstract } from '@analytics/services/manager';

@Component({
  selector: 'j-deposit-interests-details',
  templateUrl: './deposit-interests-details.component.html',
  styleUrls: ['./deposit-interests-details.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DepositInterestsDetailsComponent implements OnInit {
  @Input() transaction: IDepositInterestTransaction;
  @Input() iban: string = '';
  @Input() depositName: string = '';

  date$: Observable<string>;

  groups: {
    title: string;
    data: { label: string; value: string; isCertificate?: boolean }[];
  }[];

  constructor(
    private analytics: AnalyticsManagerAbstract,
    private depositsService: DepositsService,
    private filesService: FilesService,
    private maskPipe: MaskPipe,
    private moneyPipe: MoneyPipe,
    private notifierService: NotifierService,
    private statement2Service: Statement2Service,
    private translateService: TranslateService
  ) {}

  ngOnInit() {
    this.mapDate();
    this.mapGroups();
  }

  onDownload() {
    this.analytics.logEvent('RewardDetails_Download_Click', {
      version: 1,
      type: 'PDF'
    });

    this.notifierService.notify(
      'info',
      this.translateService.instant(this.mapDetailsLabel('DOWNLOADING'))
    );

    const name = this.translateService.instant(
      this.mapDetailsLabel('FILENAME'),
      {
        name: this.depositName,
        period: dayjs(this.transaction.documentDate).format(UI_DATE_FORMAT),
      }
    );

    this.depositsService
      .downloadDepositInterestById(this.iban, this.transaction.id)
      .pipe(take(1))
      .subscribe({
        next: (blob) => {
          this.analytics.logEvent('RewardDetails_Download_Result', {
            version: 1,
            status: 200,
          });
          this.filesService.downloadFile(blob, `${name}.pdf`);
        },
        error: (error) => {
          this.analytics.logEvent('RewardDetails_Download_Result', {
            version: 1,
            status: 502,
            error,
          });
        },
      });
  }

  onKpnDownload() {
    this.analytics.logEvent('RewardDetails_KPNCert_Click', {
      version: 1,
    });

    this.notifierService.notify(
      'info',
      this.translateService.instant('DEPOSITS.KPN.DOWNLOADING')
    );

    const name = this.translateService.instant('DEPOSITS.KPN.FILENAME_BY_ID', {
      date: dayjs(this.transaction.documentDate).format(UI_DATE_FORMAT),
    });

    this.depositsService
      .downloadCertificate({
        iban: this.iban,
        interestId: this.transaction.id,
      })
      .subscribe({
        next: (blob) => {
          this.analytics.logEvent('RewardDetails_KPNCert_Result', {
            version: 1,
            status: 200,
          });
          this.filesService.downloadFile(blob, `${name}.pdf`);
        },
        error: (error) => {
          this.analytics.logEvent('RewardDetails_KPNCert_Result', {
            version: 1,
            status: 502,
            error,
          });
        },
      });
  }

  private mapDate() {
    this.date$ = this.statement2Service.mapTransactionDate(
      this.transaction.documentDate
    );
  }

  private mapGroups() {
    this.groups = [
      {
        title: this.mapMainLabel('TITLE'),
        data: this.generateMainData(),
      },
      {
        title: this.mapDetalizationLabel('TITLE'),
        data: this.generateDetalizationData(),
      },
    ];
  }

  private generateMainData() {
    const data = [];

    data.push({
      label: this.mapMainLabel('IBAN'),
      value: of(
        this.maskPipe.transform(
          this.transaction.interestAccount,
          Masks.account.mask
        )
      ),
    });

    return data;
  }

  private generateDetalizationData() {
    const kpn$ = this.transaction.taxed
      ? of(
          `${this.transaction.interestTax}%  ${this.moneyPipe.transform(
            this.transaction.totalTaxes
          )}`
        )
      : this.translateService.get(this.mapDetalizationLabel('NO_KPN'));

    const data = [
      {
        label: this.mapDetalizationLabel('TOTAL'),
        value: of(this.moneyPipe.transform(this.transaction.totalInterest)),
      },
      {
        isCertificate: true,
        label: this.mapDetalizationLabel('KPN'),
        value: kpn$,
      },
      {
        label: this.mapDetalizationLabel('TRANSFERRED'),
        value: of(this.moneyPipe.transform(this.transaction.interest)),
      },
    ];

    return data;
  }

  private mapMainLabel(key: string) {
    return this.mapDetailsLabel(`MAIN.${key}`);
  }

  private mapDetalizationLabel(key: string) {
    return this.mapDetailsLabel(`DETALIZATION.${key}`);
  }

  private mapDetailsLabel(key: string) {
    return `DEPOSITS.INTERESTS.DETAILS.${key}`;
  }
}
