import { Component, OnDestroy } from '@angular/core';

import { Subscription } from 'rxjs';

import { FeatureFlagsService, FEATURE_FLAG } from '@feature-flags/index';

const TABS = [
  {
    label: 'DEPOSITS.STATEMENT.TITLE',
    link: 'statement',
  },
  {
    label: 'DEPOSITS.INTERESTS.TITLE',
    link: 'interests',
    isRewards: true,
  },
  {
    label: 'DEPOSITS.TERMS.TITLE',
    link: 'terms',
  },
  {
    label: 'DEPOSITS.REQUISITES.TITLE',
    link: 'requisites',
  },
];

@Component({
  selector: 'j-deposit-tabs',
  templateUrl: './deposit-tabs.component.html',
  styleUrls: ['./deposit-tabs.component.scss'],
})
export class DepositTabsComponent implements OnDestroy {
  tabs;
  subscription = new Subscription();

  constructor(private featureFlagsService: FeatureFlagsService) {
    this.initFeatureFlags();
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  private initFeatureFlags() {
    this.subscription.add(
      this.featureFlagsService.getFeatureFlags().subscribe((flags) => {
        if (this.featureFlagsService.isDisabled(FEATURE_FLAG.DEPOSIT_REWARDS)) {
          this.tabs = TABS.filter((tab) => !tab.isRewards);
        } else {
          this.tabs = [...TABS];
        }
      })
    );
  }
}
