import { Component, EventEmitter, Input, Output } from '@angular/core';

import { AnalyticsManagerAbstract } from '@analytics/services/manager';
import { IStatementDates } from '@filter/filter.model';

@Component({
  selector: 'j-deposit-interests-filter',
  templateUrl: './deposit-interests-filter.component.html',
  styleUrls: ['./deposit-interests-filter.component.scss'],
})
export class DepositInterestsFilterComponent {
  @Input() filterDates: IStatementDates;

  @Output() filtered = new EventEmitter<IStatementDates>();

  @Output() downloaded = new EventEmitter();
  @Output() kpnDownloaded = new EventEmitter();

  constructor(private analytics: AnalyticsManagerAbstract) {}

  onFilter(dates: IStatementDates) {
    this.filtered.emit(dates);
  }

  onKpnDownload() {
    this.analytics.logEvent('DepositMain_KPNCert_Click', { version: 1 });
    this.kpnDownloaded.emit();
  }

  onDownload() {
    this.analytics.logEvent('MainReward_Download_Click', {
      version: 1,
      type: 'PDF',
    });
    this.downloaded.emit();
  }
}
