import {
  ChangeDetectionStrategy,
  Component,
  Input,
  OnInit,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

import { Observable, of } from 'rxjs';

import dayjs from 'dayjs';

import { IDepositInterestTransaction } from '../../models';

@Component({
  selector: 'j-deposit-interests-transaction',
  templateUrl: './deposit-interests-transaction.component.html',
  styleUrls: ['./deposit-interests-transaction.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DepositInterestsTransactionComponent implements OnInit {
  @Input() transaction: IDepositInterestTransaction;
  @Input() depositName: string;

  date$: Observable<string>;

  constructor(private translateService: TranslateService) {}

  ngOnInit() {
    this.mapDate();
  }

  private mapDate() {
    const date = dayjs(this.transaction.documentDate);

    if (date.isSame(dayjs(), 'day')) {
      this.date$ = this.translateService.get('DEPOSITS.INTERESTS.DATE.TODAY', {
        datetime: date.format('HH:mm'),
      });
    } else if (
      dayjs(this.transaction.documentDate).isSame(
        dayjs().subtract(1, 'day'),
        'day'
      )
    ) {
      this.date$ = this.translateService.get(
        'DEPOSITS.INTERESTS.DATE.YESTERDAY',
        {
          datetime: date.format('HH:mm'),
        }
      );
    } else {
      this.date$ = of(date.format('DD MMMM HH:mm'));
    }
  }
}
