import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Subscription } from 'rxjs';
import { finalize, take } from 'rxjs/operators';

import { TranslateService } from '@ngx-translate/core';
import { NotifierService } from 'angular-notifier';
import { MaskPipe } from 'ngx-mask';

import { Deposit } from '@accounts/account.model';

import { CompanyService } from '@company/company.service';

import { Masks } from '@shared/masked/masked';
import { FilesService } from '@shared/services';
import { BankFromAccountPipe, FormatIbanPipe } from '@shared/pipes';

import { DepositsService } from '../../services';
import { PAGE_COLS_CLASSLIST } from '../../config';

interface RequisitesItem {
  label: string;
  value: string;
  valueToCopy?: string;
}

@Component({
  selector: 'j-deposit-requisites',
  templateUrl: './deposit-requisites.component.html',
  styleUrls: ['./deposit-requisites.component.scss'],
})
export class DepositRequisitesComponent implements OnDestroy {
  colsClasslist = PAGE_COLS_CLASSLIST;

  loadingCount = Array(6);
  isReady = false;
  isDownloading = false;
  iban: string;

  textToCopy: string;
  list: RequisitesItem[];

  subscription = new Subscription();

  constructor(
    private route: ActivatedRoute,
    private depositsService: DepositsService,
    private companyService: CompanyService,
    private maskPipe: MaskPipe,
    private formatIban: FormatIbanPipe,
    private bankFromAccount: BankFromAccountPipe,
    private translateService: TranslateService,
    private notifierService: NotifierService,
    private filesService: FilesService
  ) {
    this.route.parent.params.pipe(take(1)).subscribe((params) => {
      this.iban = params.iban;
      this.initSubscriptions();
    });
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  onCopy() {
    this.notifierService.notify(
      'default',
      this.translateService.instant(this.mapTranslate('COPIED'))
    );
  }

  onDownload() {
    this.isDownloading = true;

    this.depositsService
      .getRequisitesPdf(this.iban)
      .pipe(
        take(1),
        finalize(() => (this.isDownloading = false))
      )
      .subscribe((blob) => {
        this.filesService.downloadFile(blob, `${this.iban}.pdf`);
      });
  }

  private initSubscriptions() {
    this.depositsService
      .getDepositByIban(this.iban)
      .pipe(take(1))
      .subscribe((deposit) => {
        this.list = this.generateList(deposit);
        this.textToCopy = this.generateTextToCopy();

        this.isReady = true;
      });

    this.subscription.add(
      this.translateService.onLangChange.subscribe(() => {
        this.generateTextToCopy();
      })
    );
  }

  private generateTextToCopy() {
    return this.list
      .map((item) => {
        if (!(item.value || item.valueToCopy)) {
          return;
        }
        return `${this.translateService.instant(item.label)}: ${
          item.valueToCopy || item.value
        }`;
      })
      .join('\n');
  }

  private mapListTranslate(key: string) {
    if (!key) {
      return '';
    }

    return this.mapTranslate(`LIST.${key}`);
  }

  private mapTranslate(key: string) {
    return `DEPOSITS.REQUISITES.${key}`;
  }

  private generateList(deposit: Deposit) {
    return [
      {
        label: this.mapListTranslate('COMPANY'),
        value: this.companyService.currentCompany.name,
      },
      {
        label: this.mapListTranslate('BIN'),
        value: this.maskPipe.transform(
          this.companyService.currentCompany.bin,
          Masks.iin.mask
        ),
        valueToCopy: this.companyService.currentCompany.bin,
      },
      {
        label: this.mapListTranslate('KBE'),
        value: this.companyService.currentCompany.kod,
      },
      {
        label: this.mapListTranslate('IBAN'),
        value: this.formatIban.transform(deposit.iban),
        valueToCopy: deposit.iban,
      },
      {
        label: this.mapListTranslate('BANK'),
        value: this.bankFromAccount.transform(deposit.iban, 'name'),
      },
      {
        label: this.mapListTranslate('BIC'),
        value: this.bankFromAccount.transform(deposit.iban, 'bic'),
      },
    ];
  }
}
