import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Observable, Subscription } from 'rxjs';
import { take } from 'rxjs/operators';

import { Deposit } from '@accounts/account.model';

import { PAGE_COLS_CLASSLIST } from '../../config';
import { DepositsService } from '../../services';

@Component({
  selector: 'j-deposit',
  templateUrl: './deposit.component.html',
  styleUrls: ['./deposit.component.scss'],
})
export class DepositComponent {
  iban: string;
  deposit$: Observable<Deposit>;

  isLoading: boolean = true;

  subscription = new Subscription();
  colsClasslist = PAGE_COLS_CLASSLIST;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private depositsService: DepositsService
  ) {
    this.route.params.pipe(take(1)).subscribe((params) => {
      this.iban = params.iban;
      this.deposit$ = this.depositsService.getDepositByIban(this.iban);
    });
  }

  onClose() {
    this.router.navigate(['/accounts']);
  }
}
