import { DOCUMENT } from '@angular/common';
import { Component, Inject, Input, OnDestroy } from '@angular/core';

import { asideAnimations } from '@core/animations';

import {
  IDepositInterests,
  IDepositInterestTransaction,
} from '../../models';

@Component({
  selector: 'j-deposit-interests-statement',
  templateUrl: './deposit-interests-statement.component.html',
  styleUrls: ['./deposit-interests-statement.component.scss'],
  animations: asideAnimations,
})
export class DepositInterestsStatementComponent implements OnDestroy{
  @Input() statement: IDepositInterests;
  @Input() iban: string;
  @Input() depositName: string;

  activeTransaction: IDepositInterestTransaction;

  constructor(@Inject(DOCUMENT) private document: Document) {}
  
  ngOnDestroy(): void {
    this.document.body.classList.remove('overflow-hidden');
  }

  onClick(transaction: IDepositInterestTransaction) {
    this.document.body.classList.add('overflow-hidden');
    this.activeTransaction = transaction;
  }

  onClose() {
    this.activeTransaction = null;
    this.document.body.classList.remove('overflow-hidden');
  }
}
