import { Injectable } from '@angular/core';
import { HttpHeaders, HttpParams } from '@angular/common/http';

import { distinctUntilChanged, filter, map } from 'rxjs/operators';

import { ApiService } from '@api/api.service';

import { mapParams } from '@utils/api';

import { Deposit } from '@accounts/account.model';
import { AccountsService } from '@accounts/services';

import { DEPOSIT_CERTIFICATE_TYPE } from '@core/models';
import { CompanyService } from '@company/company.service';

import { IStatement2 } from '@statement2/models';

import {
  IDepositInterests,
  DepositTerms,
  IDepositInterestFilter,
  IDepositFilter,
  IDepositCertificateParams,
} from '../models';

@Injectable({
  providedIn: 'root',
})
export class DepositsService {
  constructor(
    private accountsService: AccountsService,
    private companyService: CompanyService,
    private apiService: ApiService
  ) {}

  getStatement(iban: string, filter: IDepositFilter) {
    const url = `companies/${this.companyService.currentId}/accounts/${iban}/statements`;
    const params = mapParams(filter);

    return this.apiService.getObservable<IStatement2>(url, null, params);
  }

  downloadStatement(iban: string, filter: IDepositFilter, fileType: string) {
    const url = `companies/${this.companyService.currentId}/accounts/${iban}/statements`;

    let headers = new HttpHeaders();
    headers = headers.set('Accept-File', fileType);
    headers = headers.set('Content-Type', 'application/octet-stream');

    const params = mapParams(filter);

    let extension = fileType.substring(
      fileType.lastIndexOf('/') + 1,
      fileType.length
    );

    if (extension === '1c') {
      extension += '.txt';
    }

    return this.apiService
      .getObservable<IStatement2>(url, headers, params, {
        responseType: 'blob',
      })
      .pipe(
        map((file) => {
          const filename = `${iban}.${extension}`;
          return {
            file,
            filename,
          };
        })
      );
  }

  getRequisitesPdf(iban: string) {
    const url = `companies/${this.companyService.currentId}/accounts/${iban}/requisites`;

    let headers = new HttpHeaders();
    headers = headers.set('Accept-File', 'application/pdf');
    headers = headers.set('Content-Type', 'application/octet-stream');

    const params = new HttpParams();
    const extras = {
      responseType: 'blob',
    };

    return this.apiService.getObservable(url, headers, params, extras);
  }

  getDepositInterests(iban: string, filter: IDepositInterestFilter) {
    const url = `deposit-service/companies/${this.companyService.currentId}/deposits/${iban}/interests`;
    const params = mapParams(filter);

    return this.apiService.getV2Observable<IDepositInterests>(
      url,
      null,
      params
    );
  }

  downloadDepositInterests(iban: string, filter: IDepositInterestFilter) {
    const url = `deposit-service/companies/${this.companyService.currentId}/deposits/${iban}/interests:export`;
    const params = mapParams(filter);
    const extras = {
      responseType: 'blob',
    };

    return this.apiService.getV2Observable<IDepositInterests>(
      url,
      null,
      params,
      extras
    );
  }

  downloadDepositInterestById(iban: string, id: string) {
    const url = `deposit-service/companies/${this.companyService.currentId}/deposits/${iban}/interests/${id}/export`;
    const extras = {
      responseType: 'blob',
    };

    return this.apiService.getV2Observable(url, null, null, extras);
  }

  downloadCertificate(params: IDepositCertificateParams) {
    const url = `deposit-service/companies/${this.companyService.currentId}/certificates`;
    const body = {
      params,
      certificateTypeCode: DEPOSIT_CERTIFICATE_TYPE.TAX_ON_DEPOSIT_INTEREST,
    };
    const extras = {
      responseType: 'blob',
    };

    return this.apiService.postV2Observable(url, body, null, null, extras);
  }

  getDepositTerms(iban: string) {
    const url = `deposit-service/companies/${this.companyService.currentId}/deposits/${iban}`;
    return this.apiService.getV2Observable<DepositTerms>(url);
  }

  getDepositByIban(iban: string) {
    return this.accountsService.getAccountsList().pipe(
      filter((list) => list.length > 0),
      distinctUntilChanged(this.areArraysEqual),
      map((list) => list.find((deposit) => deposit.iban === iban)),
      filter((deposit) => !!deposit)
    );
  }

  private areArraysEqual(a: Deposit[], b: Deposit[]) {
    return (
      a.length === b.length &&
      a.every((value, index) => value.id === b[index].id)
    );
  }
}
