import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
} from '@angular/core';

import dayjs from 'dayjs';

import {
  IDepositInterestGroup,
  IDepositInterestTransaction,
} from '../../models';

@Component({
  selector: 'j-deposit-interests-group',
  templateUrl: './deposit-interests-group.component.html',
  styleUrls: ['./deposit-interests-group.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DepositInterestsGroupComponent implements OnInit {
  @Input() group: IDepositInterestGroup;
  @Input() depositName: string;

  @Output() clicked = new EventEmitter<IDepositInterestTransaction>();

  title: string;

  constructor() {}

  ngOnInit() {
    this.mapGroupTitle();
  }

  onClick(transaction: IDepositInterestTransaction) {
    this.clicked.emit(transaction);
  }

  private mapGroupTitle() {
    this.title = dayjs(this.group.groupDate).format('YYYY');
  }
}
