import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
} from '@angular/core';
import { Masks } from '@shared/masked/masked';
import { BankAccountNamePipe } from '@shared/pipes';
import { MaskPipe } from 'ngx-mask';

import { Observable } from 'rxjs';

import { IStatement2Transaction, TRANSACTION_TYPE } from '../../models';
import { Statement2Service } from '../../services';

@Component({
  selector: 'j-statement2-details',
  templateUrl: './statement2-details.component.html',
  styleUrls: ['./statement2-details.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class Statement2DetailsComponent implements OnInit {
  @Input() transaction: IStatement2Transaction;
  @Output() closed = new EventEmitter();

  date$: Observable<string>;
  isCredit: boolean;
  groups: { title: string; data: { label: string; value: string }[] }[];

  constructor(
    private statement2Service: Statement2Service,
    private maskPipe: MaskPipe,
    private bankAccountName: BankAccountNamePipe
  ) {}

  ngOnInit() {
    this.mapDate();
    this.mapTransactionType();
    this.mapGroups();

    console.log(this.transaction);
  }

  onClose() {
    this.closed.emit();
  }

  private mapDate() {
    this.date$ = this.statement2Service.mapTransactionDate(this.transaction.documentDate);
  }

  private mapTransactionType() {
    this.isCredit = this.transaction.type === TRANSACTION_TYPE.CREDIT;
  }

  private mapGroups() {
    this.groups = [
      {
        title: this.mapMainLabel('TITLE'),
        data: this.generateMainData(),
      },
      {
        title: this.mapRecipientLabel('TITLE'),
        data: this.generateRecipientData(),
      },
    ];
  }

  private generateMainData() {
    const data = [];

    if (this.isCredit) {
      const { code, name } = this.transaction.knp;

      data.push({
        label: this.mapMainLabel('KNP'),
        value: `${code} ${name ? '-' : ''} ${name}`,
      });
    }

    data.push({
      label: this.mapMainLabel('DOCUMENT_NUMBER'),
      value: `${this.transaction.documentNumber}`,
    });

    return data;
  }

  private generateRecipientData() {
    const data = [
      {
        label: this.mapRecipientLabel('NAME'),
        value: this.transaction.correspondentName,
      },
      {
        label: this.mapRecipientLabel('BIN'),
        value: this.maskPipe.transform(
          this.transaction.correspondentBin,
          Masks.iin.mask
        ),
      },
    ];

    if (!this.isCredit) {
      data.push({
        label: this.mapRecipientLabel('ACCOUNT'),
        value: this.maskPipe.transform(
          this.transaction.correspondentAccount,
          Masks.account.mask
        ),
      });
    }

    data.push(
      {
        label: this.mapRecipientLabel('KBE'),
        value: this.transaction.kbe.code,
      },
      {
        label: this.mapRecipientLabel('BANK'),
        value: this.bankAccountName.transform(
          this.transaction.correspondentAccount
        ),
      },
      {
        label: this.mapRecipientLabel('BIC'),
        value: this.transaction.correspondentBankBic,
      }
    );

    return data;
  }

  private mapMainLabel(key: string) {
    return this.mapDetailsLabel(`MAIN.${key}`);
  }

  private mapRecipientLabel(key: string) {
    return this.mapDetailsLabel(`RECIPIENT.${key}`);
  }

  private mapDetailsLabel(key: string) {
    return `DEPOSITS.STATEMENT.DETAILS.${key}`;
  }
}
