import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Subscription } from 'rxjs';
import { map, take } from 'rxjs/operators';

import dayjs from 'dayjs';
import { TranslateService } from '@ngx-translate/core';

import { FormatIbanPipe, MoneyPipe } from '@shared/pipes';

import { convertApiDateToUiFormat } from '@utils/dates';

import { GeneralDictionaryTranslations, IMoney } from '@core/models';

import { PAGE_COLS_CLASSLIST } from '../../config';
import { DepositsService } from '../../services';
import { DepositTerms } from '../../models';

interface Info {
  label: string;
  value: string;
}

@Component({
  selector: 'j-deposit-terms',
  templateUrl: './deposit-terms.component.html',
  styleUrls: ['./deposit-terms.component.scss'],
})
export class DepositTermsComponent implements OnDestroy {
  colsClasslist = PAGE_COLS_CLASSLIST;

  subscription = new Subscription();
  terms: DepositTerms;

  termsBalance: IMoney;

  name: string;
  iban: string;
  hasError = false;

  mainInfo: Info[];
  replenishmentInfo: Info[];
  withdrawalInfo: Info[];

  mainLoadingCount = Array(5);
  otherLoadingCount = Array(2);

  constructor(
    private route: ActivatedRoute,
    private depositsService: DepositsService,
    private translateService: TranslateService,
    private moneyPipe: MoneyPipe,
    private formatIban: FormatIbanPipe
  ) {
    this.route.parent.params.pipe(take(1)).subscribe((params) => {
      this.iban = params.iban;
      this.initSubscriptions();
    });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onRetry() {
    this.initTermsSubscription();
  }

  initSubscriptions() {
    this.initTermsSubscription();

    this.subscription.add(
      this.translateService.onLangChange
        .pipe(map((event) => event.lang))
        .subscribe(() => {
          this.generateInfo();
        })
    );
  }

  initTermsSubscription() {
    this.subscription.add(
      this.depositsService.getDepositTerms(this.iban).subscribe({
        next: (terms) => {
          this.terms = terms;
          this.generateInfo();
        },
        error: () => {
          this.handleTermsError();
        },
        complete: () => {
          if (!this.terms) {
            this.handleTermsError();
          }
        },
      })
    );
  }

  private handleTermsError() {
    this.hasError = true;
  }

  private generateInfo() {
    this.name = this.extractTranslation(
      this.terms.depositType.descriptionTranslations
    );

    this.generateMainInfo();
    this.generateReplenishmentInfo();
    this.generateWithdrawalInfo();
  }

  private generateMainInfo() {
    this.mainInfo = [
      {
        label: this.mapMainLabel('RATE'),
        value: this.terms.interestRate ? `${this.terms.interestRate}%` : '',
      },
      {
        label: this.mapMainLabel('DOCUMENT_NUMBER'),
        value: this.terms.contractCode,
      },
      {
        label: this.mapMainLabel('PERIOD'),
        value: this.mapPeriodLabel(),
      },
      {
        label: this.mapMainLabel('AMOUNT'),
        value: this.moneyPipe.transform({
          amount: this.terms.contractSum,
          currency: this.terms.balance.currency,
        }),
      },
      {
        label: this.mapMainLabel('INTEREST'),
        value: this.extractTranslation(
          this.terms.interestPaymentOption.descriptionTranslations
        ),
      },
      {
        label: this.mapMainLabel('IBAN'),
        value: this.formatIban.transform(this.terms.clientAccount),
      },
    ];
  }

  private generateReplenishmentInfo() {
    this.replenishmentInfo = [
      {
        label: this.mapReplenishmentLabel('TITLE'),
        value: this.translateService.instant(
          this.terms.addPay ? 'SHARED.YES' : 'SHARED.NO'
        ),
      },
      {
        label: this.mapReplenishmentLabel('MAX_AMOUNT'),
        value: this.moneyPipe.transform({
          amount: this.terms.maxSum,
          currency: this.terms.balance.currency,
        }),
      },
    ];
  }

  private generateWithdrawalInfo() {
    this.withdrawalInfo = [
      {
        label: this.mapWithdrawalLabel('PARTIAL_WITHDRAWAL'),
        value: this.translateService.instant(
          this.terms.payOut ? 'SHARED.YES' : 'SHARED.NO'
        ),
      },
      {
        label: this.mapWithdrawalLabel('IRREDUCIBLE_AMOUNT'),
        value: this.moneyPipe.transform({
          amount: this.terms.irreducibleBalance,
          currency: this.terms.balance.currency,
        }),
      },
    ];
  }

  private mapMainLabel(key: string) {
    return this.mapLabel('MAIN', key);
  }

  private mapReplenishmentLabel(key: string) {
    return this.mapLabel('REPLENISHMENT', key);
  }

  private mapWithdrawalLabel(key: string) {
    return this.mapLabel('WITHDRAWAL', key);
  }

  private mapLabel(label: string, key: string) {
    if (!key) {
      return '';
    }

    return this.mapTranslate(`${label}.${key}`);
  }

  private mapPeriodLabel() {
    const from = convertApiDateToUiFormat(this.terms.fromDate);

    if (!this.terms.toDate) {
      return from;
    }

    const to = convertApiDateToUiFormat(this.terms.toDate);
    const count = dayjs(this.terms.toDate).diff(this.terms.fromDate, 'month');

    let translateKey;
    switch (count % 20) {
      case 1:
        translateKey = 'ONE';
        break;

      case 2:
      case 3:
      case 4:
        translateKey = 'FEW';
        break;

      default:
        translateKey = 'MANY';
        break;
    }

    const monthsLabel = this.translateService.instant(
      this.mapMainLabel(`MONTHS.${translateKey}`),
      { count }
    );

    return `${from} - ${to} <br> (${monthsLabel})`;
  }

  private extractTranslation(translations: GeneralDictionaryTranslations) {
    return translations.translations[this.translateService.currentLang];
  }

  private mapTranslate(key: string) {
    if (!key) {
      return '';
    }

    return `DEPOSITS.TERMS.${key}`;
  }
}
